import React, { Suspense } from "react";
import { Redirect, Route, Switch } from "react-router-dom";

const Pipelines = React.lazy(() =>
  import(/* webpackChunkName: "hp-monitor" */ "./pipelines")
);
const Artifacts = React.lazy(() =>
  import(/* webpackChunkName: "hp-submit" */ "./artifacts")
);
const Runs = React.lazy(() =>
  import(/* webpackChunkName: "hp-submit" */ "./runs")
);
const RecurringRuns = React.lazy(() =>
  import(/* webpackChunkName: "hp-submit" */ './recurringRuns')
);
const Experiments = React.lazy(() =>
  import(/* webpackChunkName: "hp-submit" */ './experiments')
);
const Archive = React.lazy(() =>
  import(/* webpackChunkName: "hp-submit" */ "./archive")
);

const PipeLines = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Route
        path={`${match.url}/pipelines`}
        render={(props) => <Pipelines {...props} />}
      />
      <Route
        path={`${match.url}/artifacts`}
        render={(props) => <Artifacts {...props} />}
      />
      <Route
        path={`${match.url}/runs`}
        render={(props) => <Runs {...props} />}
      />
      <Route
        path={`${match.url}/recurringruns`}
        render={(props) => <RecurringRuns {...props} />}
      />
      <Route
        path={`${match.url}/experiments`}
        render={(props) => <Experiments {...props} />}
      />
      <Route
        path={`${match.url}/archive`}
        render={(props) => <Archive {...props} />}
      />
      <Route
        path={`${match.url}/recurringruns`}
        render={(props) => <RecurringRuns {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);
export default PipeLines;
